import { type FC } from 'react'

import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/app/shared/ui'
import { cn } from '@/app/shared/lib/utils'
import Link from 'next/link'

// interface
interface IProps {
  className?: string
  title: string
  author: string
  publishedYear: number | string
  description?: string
  href?: string
}

// component
const CustomCardComponent: FC<Readonly<IProps>> = (props) => {
  const { title, author, publishedYear, description, href } = props

  // return
  return (
    <Link href={href ?? '/'} className={cn('group block')}>
      <Card className='hover:bg-accent cursor-pointer transition-colors'>
        <CardHeader>
          <CardTitle className='group-hover:text-primary text-lg transition-colors'>{title}</CardTitle>
          <CardDescription>by {author}</CardDescription>
        </CardHeader>
        <CardContent>
          <p className='text-muted-foreground text-sm'>Published: {publishedYear}</p>
          {description && <p className='text-muted-foreground mt-2 text-sm'>{description}</p>}
        </CardContent>
      </Card>
    </Link>
  )
}

export default CustomCardComponent
